import Draggable from "react-draggable";
import { useState, useRef } from "react";
import { useHistory, useParams } from "react-router-dom";
import axios from "axios";
import Update from "./Update";
import { Button } from "react-bootstrap";

function Detail(props) {
  const nodeRef = useRef(null);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [modal, setModal] = useState(false);
  const trackPos = (data) => {
    setPosition({ x: data.x, y: data.y });
  };
  // 
  const innerText = props.list; //  

  let history = useHistory(); //
  let { id } = useParams(); // url .
  //  ,   url,  id  id   
  // .
  let result = innerText.find(function (data) {
    //find   url id    .
    return data.id == id;
  });
  const deletePost = () => {
    axios.delete(`http://localhost:3001/board/${id}`).then(() => {
      console.log("success");
    });
  };

  return (
    <div className="box_background">
      <Draggable onDrag={(e, data) => trackPos(data)} nodeRef={nodeRef}>
        <div ref={nodeRef} className="box">
          <div className="information">
            <h4 className="h4">{result.name}</h4>

            <h5 className="h5">title:</h5>
            <h5 className="h5">{result.title}</h5>

            <h5 className="h5">text:</h5>
            <h5 className="h5">{result.text}</h5>

            <div className="buttonInbox">
              <a href="/" style={{ textDecoration: "none" }}>
                <Button onClick={deletePost}></Button>
              </a>

              <Button
                onClick={() => {
                  history.goBack();
                }}
              >
                
              </Button>
              {modal === true ? (
                <Update
                  name={result.name}
                  title={result.title}
                  text={result.text}
                  id={id}
                />
              ) : null}

              <Button
                onClick={() => {
                  setModal(!modal);
                }}
              >
                
              </Button>
            </div>
          </div>
        </div>
      </Draggable>
    </div>
  );
}

export default Detail;
